@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
h2 {
  padding: 0px;
  margin: 0px;
  display: inline;
}

.homepage {
  background-color: lightblue;
  display: grid;
  grid-template-columns: 40% 60%;
  padding: 1em;
  grid-column-gap: 1em;
  grid-row-gap: 1.5em;

  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  height: 600px;
}

.homepage > div {
  padding: 0px 20px;
  font-size: 30px;
  text-align: center;
  border-radius: 10px;
}

.link {
  text-decoration: none;
}

.progress {
  /* background-color: lightblue; */
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 198px;
  width: 500px;
}

.progress:hover {
  background-color: lightgreen;
}

.myProgress {
  width: 100%;
  height: 25px;
}

.Profile {
  background-color: green;
  padding-top: 10px;
}

.StartButton{
    width: 500px;
    height: 250px;
    background: #f3f0f1;
    position: relative;
    background: #f3f0f1;
    border-radius: 32px;
    text-align: center;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2em;
    font-size: 30px;
}

.StartButton-text {
    line-height: 100px;
    font-size: 32px;
    font-weight: semibold;
  }

.StartButton {
    /* box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8), 6px 6px 10px rgba(0, 0, 0, 0.2); */
    color: #6f6cde;
  }
.StartButton:hover {
    opacity: 0.5;
    background-color: lightgreen;
    /* box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8), 6px 6px 10px rgba(0, 0, 0, 0.2); */
  }
.StartButton:active {
    opacity: 1;
    /* box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5), inset 8px 8px 16px rgba(0, 0, 0, 0.1); */
    /* color: #79e3b6; */
  }
.video-title-container {
  grid-area: title;
  background-color: lightblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 6em;
  text-align: center;
  width: 1000px;
}

.video-next {
  grid-area: next;
  width: 10em;
  margin-left: auto;
  margin-right: auto;
  background-color: lightblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 4em;
  text-align: center;
  margin-top: 15em;
}

.video-home {
  grid-area: home;
  width: 10em;
  margin-left: auto;
  margin-right: auto;
  background-color: lightblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 4em;
  text-align: center;
  margin-top: 150em;
}

.video-container {
  grid-area: video;
  margin-left: auto;
  margin-right: auto;
  height: 7em;
}

.video {
  width: 700px;
}

.videoPage {
  display: grid;
  grid-template-areas:
    "title title"
    "video video"
    "video video"
    "video video"
    "home  next";
  grid-gap: 47em;
  grid-row-gap: 0em;
  row-gap: 0em;
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
  height: 600px;
  background-color: lightblue;
}

.video-text {
  font-size: 1.5em;
}

.video-title {
  font-size: 3em;
}

.LeaderBoard {
  background-color: lightblue;
  height: 600px;
  display: grid;
  grid-template-columns: auto;
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
  grid-gap: 3em;
}

.leaderboard-title-container {
  background-color: lightblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 6em;
  text-align: center;
}

.leaderboard-home-container {
  background-color: lightblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 6em;
  text-align: center;
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.leaderboard-container {
  width: 600px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.row {
  position: relative;
  display: block;
  width: 100%;
  height: 60px;
  border-bottom: 1px solid #afafaf;
}

.name {
  position: relative;
  display: inline-block;
  width: 75%;
  line-height: 45px;
}

.score {
  position: relative;
  display: inline-block;
  width: 25%;
}

.row:nth-child(1) {
  background: gold;
}

.row:nth-child(2) {
  background: #c0c0c0;
}

.row:nth-child(3) {
  background: #cd7f32;
}

.leaderboard-title {
  font-size: 3em;
}

.leaderboard-home {
  font-size: 2em;
}

.name-text {
  margin-left: 20px;
  font-size: 1.5em;
}

.transition-message {
  grid-area: message;
  background-color: lightblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 6em;
  text-align: center;
  margin-top: 2em;
}

.transition-next {
  grid-area: next;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: 7em;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  vertical-align: middle;
}

.transition-home {
  /* grid-area: home; */
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: 7em;
  text-align: center;
}

.Transition {
  max-width: 1000px;
  height: 600px;
  margin-left: auto;
  margin-right: auto;
  background-color: lightblue;
  display: grid;
  grid-template-areas:
    "message message"
    "home next";
  grid-gap: 3em;
  grid-row-gap: 1em;
  row-gap: 1em;
}

.transition-text {
  font-size: 1.5em;
}

.transition-title {
  font-size: 3em;
}

.plan {
  list-style-type: none;
  margin: 2em;
}

.plan > li {
  font-size: 2em;
}

.trainer-plan-title-container {
  grid-area: title;
  background-color: lightblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 6em;
  text-align: center;
}

.trainer-plan-container {
  grid-area: plan;
  height: 15em;
  text-align: center;
}

.trainer-no-container {
  grid-area: no;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: 4em;
  text-align: center;
  margin-top: 150em;
}

.trainer-yes-container {
  grid-area: yes;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: 4em;
  text-align: center;
  margin-top: 1em;
}

.Trainer {
  display: grid;
  grid-template-areas:
    "title title"
    "plan  plan"
    "no    yes";
  grid-gap: 3em;
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
  background-color: lightblue;
  height: 600px;
}

.trainer-text {
  font-size: 1.5em;
}

.trainer-title {
  font-size: 3em;
}

.makePlan-plan-title-container {
  grid-area: title;
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 6em;
  text-align: center;
}

.makePlan-plan-container {
  grid-area: plan;

  height: auto;
  text-align: center;
  display: grid;
  grid-template-columns: auto;
  grid-row-gap: 2em;
  row-gap: 2em;
  padding: 2em;
}

.makePlan-back-container {
  grid-area: back;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  height: 4em;
  text-align: center;
  margin-top: 1em;
}

.makePlan-home-container {
  grid-area: home;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  height: 4em;
  text-align: center;
  margin-top: 1em;
}

.makePlan {
  display: grid;
  grid-template-areas:
    "title title"
    "plan  plan"
    "back  home";
  grid-gap: 3em;
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
  background-color: lightblue;
  height: 600px;
}

.makePlan-text {
  font-size: 1.7em;
}

.makePlan-title {
  font-size: 3em;
}

.progress-title-container {
  border: 1px solid rgba(0, 0, 0, 0.8);
  height: 6em;
  text-align: center;
}

.progress-header {
  text-align: center;
}

.exercise {
  text-align: center;
}

.progress-home-container {
  height: 6em;
  text-align: center;
  vertical-align: middle;
  width: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10em;
  -webkit-justify-content: center;
          justify-content: center;
}

.progress-title {
  font-size: 3em;
}

.progress-home {
  font-size: 2em;
}

.progress-page {
  width: 1000px;
  background-color: lightblue;
  margin-left: auto;
  margin-right: auto;
  height: 600px;
}

.progress-bar-container {
  display: -webkit-flex;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  width: 400px;
  margin-top: 1em;
  -webkit-justify-content: center;
          justify-content: center;
}
.myProgress {
  width: 100%;
  height: 25px;
}

